.total-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #151515;
  color: white;
  min-height: 100vh;
  box-sizing: border-box;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 12px;
}

.page-title {
  font-size: 1.6rem;
  color: #434BDB;
  text-align: center;
  margin: 60px 0 30px 0;
}

.main-container {
  width: 100%;
  max-width: 960px;
  padding: 0 12px;
}

.main-title {
  font-size: 3rem;
  text-align: center;
  margin-bottom: 60px;
}

.research-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

.research-title h1 {
  font-size: 1.8rem;
  margin-bottom: 10px;
}

.research-contents {
  background-color: #333;
  padding: 15px;
  border-radius: 8px;
  color: #ccc;
  font-size: 1.4rem;
}

/* Tablet 버전 (768px - 991px) */
@media (max-width: 991px) {
  .main-container {
    width: 90%;
    padding: 0 10px;
  }

  .page-title {
    font-size: 1.4rem;
  }

  .main-title {
    font-size: 2.5rem;
  }

  .research-title h1 {
    font-size: 1.5rem;
  }

  .research-contents {
    font-size: 1.2rem;
  }
}

/* Mobile 버전 (최대 767px) */
@media (max-width: 767px) {
  .main-container {
    width: 100%;
    padding: 0 10px;
  }

  .page-title {
    font-size: 1.2rem;
  }

  .main-title {
    font-size: 2rem;
    margin-bottom: 30px;
  }

  .research-title h1 {
    font-size: 1.25rem;
  }

  .research-contents {
    font-size: 1rem;
    padding: 10px;
  }
}