.home-container {
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  height: 100vh;
  padding: 0 20px 20px 20px;
  box-sizing: border-box;
  background-color: #151515;
}

.home-title {
  font-size: 4.8em;
  margin: 0;
  padding: 16%;
  color: white;
}

/* Tablet 버전 (768px - 991px) */
@media (max-width: 991px) {
  .home-title {
    font-size: 3.6em;
    padding: 12%;
  }
}

/* Mobile 버전 (최대 767px) */
@media (max-width: 767px) {
  .home-container {
    align-items: center;
    /* 수직 중앙 정렬 */
    justify-content: center;
    /* 수평 중앙 정렬 */
  }

  .home-title {
    font-size: 2.4em;
    padding: 0;
    text-align: center;
  }

  @media (max-width: 480px) {
    .home-container {
      align-items: center;
      justify-content: center;
    }

    .home-title {
      font-size: 1.8em;
    }
  }
}